<template>
  <div class="customers container">
      <alert v-if="alertmsg" :message="alertmsg"></alert>
      <h1 class="page-header">用户管理系统</h1>
      <!-- 1.添加搜索框 -->
      <input type="text" class="form-control" placeholder="搜索" v-model="filterInput">
      <br>
      <table class="table table-striped">
          <thead>
              <tr>
                  <th>姓名</th>
                  <th>电话</th>
                  <th>邮箱</th>
              </tr>
          </thead>
          <tbody>
              <!-- 3.通过 filterBy函数进行渲染 -->
              <tr v-for="customer in filterBy(customers,filterInput)" :key="customer.id">
                  <td>{{customer.name}}</td>
                  <td>{{customer.phone}}</td>
                  <td>{{customer.email}}</td>
                  <td><router-link :to="'/customer/' + customer.id" class="btn btn-default">详情</router-link></td>
              </tr>
          </tbody>
      </table>
  </div>
</template>

<script>
import alert from './alert'
export default {

  name: 'customers',
  data () {
    return {
     customers: [],
     alertmsg: "",
    //  2.搜索框绑定数据
     filterInput: ""
    }
  },
  created() {
      if(this.$route.query.alertmsg) {
          this.alertmsg = this.$route.query.alertmsg
      }
      this.fetchCustomers()
  },
    // 页面更新时再次调用该方法
  updated() {
      this.fetchCustomers()
  },
  methods: {
      fetchCustomers(){
          this.$http.get("http://localhost:3000/users").then(res => {
            this.customers = res.body
            
          })
      },
    //   4.搜索匹配函数
    filterBy(customers, value){
        return customers.filter(customer => {
            return customer.name.match(value)
        })
    }
  },
  components: {alert}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



