<template>
  <div class="add container">
    
    <h1 class="page-head">添加用户</h1>
    <form action v-on:submit="addCustomer">
      <div class="well">
        <h4>用户信息</h4> 
        <div class="form-group">
          <label for="name">姓名</label>
          <input id="name" type="text" class="form-control" placeholder="name" v-model="customer.name">
        </div>
        <div class="form-group">
          <label for="phone">电话</label>
          <input id="phone" type="text" class="form-control" placeholder="phone" v-model="customer.phone">
        </div>
        <div class="form-group">
          <label for="email">邮箱</label>
          <input id="email" type="text" class="form-control" placeholder="email" v-model="customer.email">
        </div>
        <div class="form-group">
          <label for="education">学历</label>
          <input
          id="education"
            type="text"
            class="form-control"
            placeholder="education"
            v-model="customer.education"
          >
        </div>
        <div class="form-group">
          <label for="graduationschool">毕业学校</label>
          <input
            id="graduationschool"
            type="text"
            class="form-control"
            placeholder="graduationschool"
            v-model="customer.graduationschool"
          >
        </div>
        <div class="form-group">
          <label for="profession">职业</label>
          <input
          id="profession"
            type="text"
            class="form-control"
            placeholder="profession"
            v-model="customer.profession"
          >
        </div>
        <div class="form-group">
          <label for="profile">个人简介</label>
          <!-- <input type="text" class="form-control" placeholder="profile" v-model="customer.profile"> -->
          <textarea rows="10" class="form-control" v-model="customer.profile" id="profile"></textarea>
        </div>
        <alert v-if="alert" :message="alert"></alert>
        <button type="submit" class="btn btn-primary">添加</button>
      </div>
    </form>
  </div>
</template>

<script>
import alert from './alert'
export default {
  name: "add",
  data() {
    return {
      customer: {},
      alert: ''
    };
  },
  methods: {
    addCustomer(e) {
      if(!this.customer.name || !this.customer.phone || !this.customer.email){
          // console.log("~~")
          this.alert = "请填写对应信息"
      }else{
          let newCustomer = {
              name: this.customer.name,
              phone: this.customer.phone,
              email: this.customer.email,
              education: this.customer.education,
              graduationschool: this.customer.graduationschool,
              profession: this.customer.profession,
              profile: this.customer.profile
          }

          this.$http.post("http://localhost:3000/users", newCustomer).then(res => {
            //   console.log(res)
            this.$router.push({path: "/",query: {alertmsg: "用户信息添加成功！"}})
          })
          
      }
      e.preventDefault();
    }
  },
  components: {
    alert
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



